.header {
    padding: 25px;
    max-width: 970px;
    width: 100%;
    margin: 0 auto;
    font-family: neue-haas-grotesk-display;
    box-sizing: border-box;

    h1 {
        text-align: center;
        padding-bottom: 2.1875rem;
        padding-top: 2.5rem;
        letter-spacing: .063rem;
        font-size: 3rem;
        line-height: 3rem;

        @media(min-width: 920px) {
            font-size: 5.0625rem;
            line-height: 1.875rem;
        }
    }

    h2 {
        color: rgb(130, 137, 161);
        margin-top: 0;
        margin-bottom: 0;
        padding-top: 0;
    }

    h3 {
        font-size: 1.6875rem;
        line-height: 1.875rem;
        letter-spacing: 0.03375rem;
    }

    .columns {
        >div:last-child {
            display: none;
        }

        @media(min-width: 821px) {
            gap: 2.5%;
            display: grid;
            grid-template-columns: 3fr 4fr;
            vertical-align: top;
            margin-bottom: 3rem;

            >div:last-child {
                border-left: 1px solid black;
                display: block;
                padding-left: 5%;
            }
        }

        div.summary {
            padding-right: 5%;
            display: grid;
            padding-bottom: 5%;
            border-bottom: 1px solid black;
            grid-template-columns: 1fr 3fr 2fr;

            img {
                width: 100%;
                height: auto;
            }

            >div:first-child {
                font-size: 5.4375rem;
                font-weight: 900;
                margin-top: -1.25rem;
                text-align: center;
                margin-bottom: 1.25rem;
            }

        }
    }
}

.navigation {
    justify-content: space-between;
    border-top: 6px solid;
    border-bottom: .188rem solid;
    padding-top: 1rem;
    padding-bottom: 1rem;
    display: flex;
    margin-bottom: 56px;

    >a {
        color: rgb(26, 72, 196);
        font-size: 0.9375rem;
        line-height: 1.5625rem;
        font-weight: 900;
        text-transform: uppercase;
        text-decoration: none;

    }

    .navigation>a:global(".arrow-right") {
        justify-content: flex-end;
    }
}


.table {

    background-color: rgb(237, 235, 235);
    width: 100%;
    font-family: neue-haas-grotesk-display;
    padding: 1rem 15px;
    box-sizing: border-box;

    >h2 {
        display: block;
        padding-top: 1.5rem;
        padding-bottom: 1rem;
        max-width: 970px;
        margin: 15px auto;
        font-size: 1.875rem;
        line-height: 1.875rem;

        >span {
            float: none;
            display: block;
            text-transform: uppercase;
            font-weight: normal;
            font-size: 1rem;
            line-height: 1.875rem;

            @media(min-width: 821px) {
                display: inline;
                float: right;
            }
        }
    }

    .row {
        >div {
            display: none;

            >img {
                width: 100%;
                height: auto;
                display: block;
            }

            border-bottom: 1px solid black;
        }

        >div:nth-child(1),
        >div:nth-child(2),
        >div:nth-child(3),
        >div:nth-child(4) {
            display: table-cell;
        }

        >div:nth-child(3) {
            width: 9%;
            text-align: center;
        }

        >div.thin:nth-child(1) {
            width: 10%;
        }

        >div:nth-child(2) {
            width: 21%;
        }

        .row>div:nth-child(4) {
            width: 60%;
        }

        >div:first-child {
            font-size: 1.5rem;
            font-weight: 900;
            background-color: black;
            color: white;
            text-align: center;
            border-bottom: 1px solid white;
        }

        @media(min-width: 821px) {
            >div:first-child {
                font-size: 2rem;
            }
        }


        >div:nth-child(5),
        >div:nth-child(7) {
            background-color: rgb(237, 235, 235);
        }



        >div:nth-child(3) {
            padding-left: 0rem;
            padding-right: 0rem;
        }

        >div:first-child {
            font-size: 1.5rem;
            font-weight: 900;
            background-color: black;
            color: white;
            text-align: center;
            border-bottom: 1px solid white;
        }

    }


    .row:nth-child(3)>div:nth-child(2) {
        width: 30%;
    }

    .row:nth-child(3)>div:nth-child(4) {
        width: 51%;
    }

    .row:nth-child(3)>div:nth-child(6),
    .row:nth-child(3)>div:nth-child(7),
    .row:nth-child(3)>div:nth-child(8) {
        font-size: 2rem;
        font-weight: 900;
    }

    .row:nth-child(3)>div h3 {
        font-size: 1.4375rem;
    }

    .row:nth-child(3)>div span.artist {
        font-size: 1.25rem;
    }

    @media(min-width: 821px) {
        .row>div:nth-child(2) {
            width: 11%;
        }

        .row:nth-child(3)>div:nth-child(2) {
            width: 21%;
        }

        .row>div:nth-child(4) {
            width: 40%;
        }

        .row:nth-child(3)>div:nth-child(4) {
            width: 30%;
        }
    }

    .row,
    .headerRow {
        background-color: white;
        margin: 0 auto;
        display: table;
        table-layout: fixed;
        width: 100%;

        @media(min-width: 821px) {
            max-width: 970px;
            margin: 0 auto;
        }
    

        >div {
            vertical-align: middle;
        }

        @media(min-width: 821px) {
            >div {
                display: table-cell;
            }

        }

    }
    
    .headerRow {

        background-color: rgb(26, 72, 196);
        color: white;

        >.thinnest {
            display: none;
        }

        >div:nth-child(1) {
            display: table-cell;
            width: 100%;
            text-align: center;
        }

        >div:first-child {
            text-align: center;
        }

        background-color: rgb(26, 72, 196);
        color: white;

        >div {
            font-size: 0.71875rem;
            font-weight: bold;
            line-height: 1.125rem;
            text-transform: uppercase;
            padding-top: 0.75rem;
            padding-bottom: .75rem;
        }

        @media(min-width: 821px)
        {
            > div.thinnest {
            display: table-cell;}
            >div:nth-child(1) {
                width: 70%; text-align: left;
                padding-left: 2rem;
            }
            >div:nth-child(2) { display: none;}
        }
    }

}

h3 {
    font-size: 1.125rem;
    margin-top: 0;
    margin-bottom: 0;
    padding-top: 0;
    padding-bottom: 0;
}

.newThisWeek {
    padding: 2.5rem 0;
    text-align: center;
    color: rgb(26, 72, 196);
    font-size: 1.25rem;
    line-height: 1.5rem;
    letter-spacing: 0.0375rem;
    font-weight: 900;
}

.thin {
    width: 10%;
}

.thinnest {
    width: 7.5%;
    text-align: center;
}

.moreData {
    padding-top: 2.5rem;
    padding-bottom: 1rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 5px;

    >div {
        >p {
            text-transform: uppercase;
            font-size: 0.6875rem;
            text-align: center;
            line-height: 1.125rem;
            font-weight: 900;
        }

        h3 {
            font-size: 2.1875rem;
            line-height: 2.5625rem;
            letter-spacing: 0.05rem;
            text-align: center;
            font-weight: 900;
        }
    }
}



.new {
    padding: 0.1rem 0.4rem;
    background-color: #fff100;
    color: rgb(43, 46, 60);
    font-size: 0.6875rem;
    line-height: 1.125rem;
    font-weight: 900;
    letter-spacing: 0.05125rem;
}

.rentry {
    display: block;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    line-height: 1.2;
    letter-spacing: 0;
}

.loser,
.riser,
.same,
.award {
    width: 1.625rem;
    height: 1.625rem;
    display: inline-block;
}

.award {
    width: 1.4rem;
    height: 1.4rem;
}

.topentry {
    width: 2.8rem;
    height: 2.8rem;
}

.artist {
    font-size: 0.9375rem;
    display: block;
    line-height: 1.3125rem;
    max-width: 95%;
}